/* -----------------
Global Styles
----------------- */

//BASICS
html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  height: 100% !important;
}

body {
  margin: 0;
  padding: rem(65px 0);

  @include grid-media($small-screen) {
    padding: rem(65px 0);
  }

  &.add-padding {
    padding: rem(65px 50px 65px 0);

    @include grid-media($small-screen) {
      padding: rem(65px 0);
    }
  }
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

ul {
  padding: 0;
  margin: 0;
}